class Player {
    constructor(x, y, ctx) {
        this.x = x;
        this.y = y;
        this.height = 32;
        this.width = 32;
        this.speed = 3;
        this.color = "blue";
        this.ctx = ctx;
    }

    update() {
        if (leftPressed === true) {
            this.x -= this.speed;
        }
        if (rightPressed === true) {
            this.x += this.speed;
        }
    }

    draw() {
        ctx.beginPath();
        ctx.rect(this.x - this.width / 2, this.y - this.height / 2, this.width, this.height);
        ctx.fillStyle = "black";
        ctx.fill();
        ctx.closePath();
    }

}

export default Player;